html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  color: #ededf4;
  background: #0075c4;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  font-family: 'Handlee', sans-serif; }

.main {
  display: flex;
  flex-direction: column;
  align-content: space-between;
  max-width: 375px;
  max-width: 100%;
  background: #0075c4;
  padding: 32px;
  max-height: 100vh; }

.header__title {
  font-size: 24px;
  text-align: center; }

.datetime {
  padding: 24px;
  text-align: center; }

.glass {
  padding: 24px;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("../assets/img/Vector.svg") center center no-repeat;
  height: 250px;
  min-width: 160px; }
  .glass__counter {
    font-size: 36px; }
  .glass__button {
    border-radius: 8px;
    max-width: 140px;
    font-size: 16px;
    padding: 8px;
    margin: 4px;
    cursor: pointer;
    letter-spacing: 0.8px;
    color: #ededf4; }
    .glass__button--add {
      background: linear-gradient(180deg, #0077BE 0%, rgba(0, 119, 190, 0) 100%);
      background-blend-mode: difference;
      border: 1px solid #ededf4;
      filter: blur(0.15px);
      box-shadow: 0 0 10px #0075c4; }
    .glass__button--remove {
      background: linear-gradient(180deg, rgba(109, 161, 193, 0) 0%, #BE4700 75.52%);
      background-blend-mode: hard-light;
      border: 1px solid #ededf4;
      margin-top: 12px; }

.glass__actions {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 16px; }

.footer {
  background: #0075c4;
  display: flex;
  justify-content: center;
  margin-top: 24px;
  font-variant: small-caps;
  font-size: 75%; }
